.app-navbar {
    height: 300px;
    background: url(/react-firebase-events-editor/static/media/header_bg.3d1abf55.png) no-repeat;
    background-size: cover;
}

.header-content {
    display: flex;
    justify-content: space-around;
}
.header-left-column {
    height: 300px;
    /* width: 20%; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.header-right-column {
    color: white;
    display: flex;
    align-items: center;
}

.heder-buttons {
    display: flex;
}

.event-time {
    display: flex;
    width: 20%;
}
.event-desc {
    display: flex;
    width: 80%;
    flex-direction: column;
}

.event-desc p {
    white-space: nowrap;
    overflow: hidden !important;
    text-overflow: ellipsis;
}

.event-delete {
    display: flex;
    width: 10%;
    justify-content: flex-end;
}

@media (max-width:768px) {
    .header-right-column {
        display: none;
    }

    .header-left-column {
        text-align: center;
    }

    .header-left-column h6 {
        font-size: 2rem;
    }
}
.remove-btn {
  margin-right: 0.5rem;
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 700ms ease-in;
}
.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0.01;
  transition: opacity 700ms ease-out;
}

